'use client';

import React, { useState } from 'react';
import DiskCard from './DiskCard';

interface Disk {
  id: number;
  disk_name: string;
  title: string;
  file_size?: string;
  extract_code?: string;
  download_url?: string;
  created_at: string;
}

interface DiskCarouselProps {
  disks: Disk[];
}

export default function DiskCarousel({ disks }: DiskCarouselProps) {
  const [currentIndex, setCurrentIndex] = useState(0);

  if (!disks || disks.length === 0) {
    return null;
  }

  // 单个网盘直接显示
  if (disks.length === 1) {
    return (
      <div className="w-full">
        <DiskCard disk={disks[0]} />
      </div>
    );
  }

  // 多个网盘显示选项卡式
  return (
    <div className="w-full">
      {/* 顶部选项卡 - 紧凑版 */}
      <div className="flex justify-center gap-2 mb-3 overflow-x-auto">
        {disks.map((disk, index) => (
          <button
            key={disk.id}
            onClick={() => setCurrentIndex(index)}
            className={`px-3 py-1.5 rounded-lg text-xs font-medium whitespace-nowrap transition-all ${
              index === currentIndex
                ? 'bg-blue-600 text-white shadow-md'
                : 'bg-gray-100 dark:bg-gray-700 text-gray-600 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-600'
            }`}
          >
            {disk.disk_name}
          </button>
        ))}
      </div>

      {/* 卡片内容区域 */}
      <div className="overflow-hidden rounded-xl">
        <div
          className="flex transition-transform duration-300 ease-out"
          style={{ transform: `translateX(-${currentIndex * 100}%)` }}
        >
          {disks.map((disk) => (
            <div key={disk.id} className="w-full flex-shrink-0">
              <DiskCard disk={disk} />
            </div>
          ))}
        </div>
      </div>

      {/* 底部点指示器 - 紧凑版 */}
      {disks.length > 1 && (
        <div className="flex justify-center gap-1.5 mt-3">
          {disks.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentIndex(index)}
              className={`transition-all duration-200 rounded-full ${
                index === currentIndex
                  ? 'w-4 h-1.5 bg-blue-600'
                  : 'w-1.5 h-1.5 bg-gray-300 dark:bg-gray-600 hover:bg-gray-400'
              }`}
              aria-label={`切换到第 ${index + 1} 个网盘`}
            />
          ))}
        </div>
      )}
    </div>
  );
}
