'use client';

import React, { useState } from 'react';

interface DiskCardProps {
  disk: {
    id: number;
    disk_name: string;
    title: string;
    file_size?: string;
    extract_code?: string;
    download_url?: string;
    created_at: string;
  };
}

export default function DiskCard({ disk }: DiskCardProps) {
  const [copied, setCopied] = useState(false);

  const handleCopy = (text: string) => {
    navigator.clipboard.writeText(text);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleDownload = () => {
    if (disk.download_url) {
      window.open(disk.download_url, '_blank');
    }
  };

  return (
    <div className="bg-gradient-to-br from-white to-gray-50 dark:from-gray-800 dark:to-gray-900 rounded-xl p-4 shadow-lg border border-gray-200 dark:border-gray-700">
      {/* 标题 */}
      <div className="text-center mb-3">
        <h3 className="text-sm sm:text-base font-bold text-gray-900 dark:text-white leading-tight">{disk.title}</h3>
      </div>

      {/* 信息区域 - 紧凑横向布局 */}
      <div className="flex items-center justify-center gap-3 sm:gap-4 mb-3 flex-wrap">
        {/* 文件大小 */}
        {disk.file_size && (
          <div className="flex items-center gap-1.5 text-xs text-gray-600 dark:text-gray-400">
            <svg className="w-3.5 h-3.5 text-blue-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 21h10a2 2 0 002-2V9.414a1 1 0 00-.293-.707l-5.414-5.414A1 1 0 0012.586 3H7a2 2 0 00-2 2v14a2 2 0 002 2z" />
            </svg>
            <span>{disk.file_size}</span>
          </div>
        )}

        {/* 提取码 */}
        {disk.extract_code && (
          <div className="flex items-center gap-1.5">
            <span className="text-xs text-gray-500 dark:text-gray-400">提取码:</span>
            <span className="text-xs font-mono font-bold text-purple-600 dark:text-purple-400 bg-purple-50 dark:bg-purple-900/30 px-1.5 py-0.5 rounded border border-purple-200 dark:border-purple-700">
              {disk.extract_code}
            </span>
            <button
              onClick={() => handleCopy(disk.extract_code!)}
              className="px-1.5 py-0.5 bg-blue-600 hover:bg-blue-700 text-white text-xs rounded transition-colors flex items-center gap-1"
            >
              {copied ? (
                <>
                  <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                  </svg>
                  <span className="hidden sm:inline">已复制</span>
                </>
              ) : (
                <>
                  <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                  </svg>
                  <span className="hidden sm:inline">复制</span>
                </>
              )}
            </button>
          </div>
        )}
      </div>

      {/* 下载按钮 - 紧凑 */}
      <div className="flex justify-center">
        <button
          onClick={handleDownload}
          disabled={!disk.download_url}
          className="px-4 py-1.5 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 disabled:from-gray-400 disabled:to-gray-500 text-white rounded-lg transition-all shadow-md hover:shadow-lg disabled:cursor-not-allowed text-xs sm:text-sm font-medium flex items-center gap-1.5"
        >
          <svg className="w-3.5 h-3.5 sm:w-4 sm:h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M9 19l3 3m0 0l3-3m-3 3V10" />
          </svg>
          <span>下载资源</span>
        </button>
      </div>
    </div>
  );
}
